nav {
  width: 100vw;
  background-color: darkmagenta;
  padding-inline: 5%;
}
.links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 1%;
}

a {
  text-decoration: none;
  color: orange;
  transition: 0.9ms;
}

li a:hover {
  font-size: 1.1em;
  color: rgb(170, 110, 0);
  transition: 0.3s;
}
